<template>
    <div class='comment marTop_50'>
    <div class='container'>
      <div class='row'>
        <div class='col-xs-12 col-sm-8'>
          <div class="panel panel-default">
              <div class="panel-body">
                  <h2 class="text-center" v-text='details.title'></h2>
                  <p class="text-center" v-text='details.date'></p>
                  <div style='background:#fff;' class="well well-lg" v-html='details.content'>
                  </div>
                  <p @click='back' class='text-center cursor'>
                    <i class='fa fa-arrow-left fa-lg'> </i>
                  </p>
              </div>
          </div>
        </div>
        <div class='col-xs-12 col-sm-4'>
          <div class="panel panel-default" style='border-bottom:none !important;'>
              <div class="panel-body">
                  <h4 class="text-danger">
                    <b>评论:</b>&nbsp;
                    <small>以下是网友评论（不代表本人支持其中的观点）。</small>
                  </h4>
                  <div class='comment_list' v-for='(item,index) in comment'>
                    <label v-text='item.user'></label>
                    <p v-text='item.text'></p>
                  </div>
                  <br />
                  <button @click='add_com' type="button" class="btn btn-info">添加评论</button>
              </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import u from '@/assets/js/utils.js'

export default {
  data () {
      return {
        msg: 'comment Vue',
        name: 'Reborn',
        details:{
          title:'标题',
          date:'2017-05-04',
          content:''
        },
        comment:[]

      }
    },
  created(){
    this.loadDetail();
  },
  components: {

  },
  mounted() {

  },
  methods:{
    foo() {
      console.log('foo')
    },
    add_com(){
      let num = u.GetUrl(9);
      let url = 'https://github.com/Reborn233/loveZQJ.github.io/issues/'+num;
      window.location = url
    },
    loadDetail(){
      let num = u.GetUrl(9);
      // console.log(num);
      let url = 'https://api.github.com/repos/Reborn233/loveZQJ.github.io/issues/'+num;
      this.$http.get(url).then(function(response){
                // 响应成功回调
                var data = response.body;
                var converter = new showdown.Converter();
                // console.log(JSON.stringify(data))
                this.details.title = data.title;
                this.details.date = data.updated_at.substring(0,10);
                this.details.content = converter.makeHtml(data.body);
                // console.log(JSON.stringify(this.details))
            }, function(response){
                // 响应错误回调
                console.log('error');
            });
      this.loadCom(num);

    },
    loadCom(num){
      let url = 'https://api.github.com/repos/Reborn233/loveZQJ.github.io/issues/'+num+'/comments';
      this.$http.get(url).then(function(response){
            // 响应成功回调
            var data = response.body;
            // console.log(JSON.stringify(data))
            for (var i = 0; i < data.length; i++) {
              this.comment.push({
                user:data[i].user.login,
                text:data[i].body
              })
            };
            // console.log(JSON.stringify(this.comment))
        }, function(response){
            // 响应错误回调
            console.log('error');
        });
    },
    back(){
      history.back();
    }
      
  }
}
</script>

<style scoped>

</style>



